.nav {
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  width: 100%;
}

.navMain {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 200px;
  top: 0;
  position: sticky;
}

.leftNav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  margin-right: 20px;
  cursor: pointer;
  font-size: 45px;
}

.searchDiv {
  position: relative;
}

.searchDiv i {
  position: absolute;
  top: 13px;
  color: gray;
  left: 190px;
}

.search {
  width: 100%;
  height: 40px;
  outline: none;
  padding: 10px;
}

.rightNav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navBtn {
  width: 100px;
  height: 40px;
  cursor: pointer;
  font-weight: 600;
  text-transform: capitalize;
}

.navBtn:hover {
  background: #f4f4f4;
  outline: none;
  border: 1px solid gray;
  border-radius: 5px;
}

.navImg {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 5px 0 5px 20px;
}
